@font-face {
  font-family: "PressStart2P";
  src: url("./fonts/PressStart2P-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "PixelMplus12";
  src: local("GoldmanBold"),
    url("./fonts/PixelMplus12-Regular.ttf") format("truetype");
}

html,
body {
  background-color: #0100a6;
  color: white;
  font-family: "PressStart2P";
}

p {
  margin: 0;
}

.group-text-connect {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.info-container {
  background-image: url("./bg.svg");
  padding: 0px 20px 20px 20px;
  border-top: 2px solid #a8ffff;
  border-bottom: 2px solid #a8ffff;
  background-color: black;
  height: 350px;
  background-position-x: center;
}

.group-container-info {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 80px;
  gap: 20px;
  transform: translateX(-73px);
}

.land-divide {
  width: 100%;
}

.detail-info {
  margin-top: 80px;
}

.info-text {
  font-size: 26px;
}

.price-info {
  font-size: 16px;
  margin: 5px 0px 15px 0px;
}

.header-content-support {
  font-size: 20px;
  text-align: center;
  padding: 0 10px 0 10px;
}

.button-switch {
  margin-top: 15px;
  font-size: 15px;
  border: 2px solid white;
  color: white;
  background-color: #0100a6;
  padding-top: 0px;
  height: 60px;
}

.custom-button {
  border-radius: 5px;
}

.container-app {
  text-align: center;
}

.header-button {
  display: flex;
  justify-content: flex-end;
  height: 104px;
}

.group-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.group-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-bottom: 1px solid #fff;
  padding: 80px;
}

.group-content {
  margin-top: 115px;
  position: relative;
  z-index: 0;
}

.group-first-content {
  padding-top: 100px;
  border-top: 1px solid #fff;
  background-image: url("./icon/bg-center.svg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  padding-bottom: 73px;
}

.gif-icon {
  width: 123px;
  height: 123px;
}

.heading-title {
  padding: 20px 0 20px 0;
  margin-top: 20px;
  font-size: 20px;
}

.btn-connect {
  color: white;
  font-size: 22px;
  height: 50px;
  padding-left: 16px;
  padding-top: 10px;
}

.btn-connect:hover {
  border-color: black;
}

.btn-connect span {
  vertical-align: text-top;
}

.btn-loading {
  border: 2px solid white;
  color: white;
  height: 46px;
  font-size: 22px;
  display: flex;
  gap: 10px;
  width: 125px;
}

.btn-grad {
  border: 2px solid white;
  color: white;
  height: 46px;
  font-size: 22px;
  width: 125px;
}

.english-content {
  margin: 0 20px 0 20px;
  text-align: center;
  font-size: 16px;
  line-height: 37px;
}

.japan-content {
  font-family: "PixelMplus12";
  margin: 0 20px 0 20px;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 40px;
}

.group-footer-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 33px;
}

.group-footer-icon a {
  display: grid;
  place-items: center;
}

.footer-content {
  font-size: 12px;
  padding-bottom: 16px;
  padding-top: 36px;
}

.heading-content {
  font-size: 24px;
  padding-bottom: 43px;
}

.heading-content-second {
  font-size: 24px;
  padding-bottom: 30px;
}

.team-role {
  padding-top: 24px;
  font-size: 18px;
  color: #a8ffff;
}

.english-content {
  padding-bottom: 50px;
  text-align: center;
  font-size: 16px;
}

.japan-content {
  font-family: "PixelMplus12";
  text-align: center;
  font-weight: 600;
}

.japan-content-mobile {
  display: none;
}

.group-image {
  display: flex;
  padding: 10px 0 20px 0;
  gap: 160px;
  justify-content: center;
  align-items: center;
}

.btn-custom {
  margin: 3px 0 3px 0;
  padding: 0;
  background-color: #fff;
}

.address-text {
  font-size: 10px;
  margin: 10px 10px 0 0;
}

.group-footer {
  margin-top: 150px;
}

.team-name {
  padding-top: 16px;
  font-size: 14px;
}

.background-image {
  position: absolute;
  inset: 0;
  z-index: -1;
  background: url("./icon//bg.svg") no-repeat;
}

.land-divide {
  height: 71px;
}

.spinner {
  margin-left: -11px;
}

.button-mint {
  justify-content: center;
  display: flex;
}

.group-button {
  padding-right: 20px;
}

.triangle-icon {
  width: 16px;
  height: 23px;
  cursor: pointer;
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .content-space {
    margin: 0 24px 0 24px;
  }

  .team-image {
    width: 195px;
    height: 195px;
  }

  .background-image {
    top: 33%;
    transform: scaleY(1);
  }

  .image_full {
    display: block;
    position: absolute;
  }

  .image_mobile {
    display: none;
  }

  .group-container-info {
    transform: translateX(-11%);
  }

  .info-container {
    background-position-x: unset;
  }
}

@media only screen and (min-width: 1024px) {
  .content-space {
    margin: 0 50px 0 50px;
  }

  .team-image {
    width: 195px;
    height: 195px;
  }

  .background-image {
    top: 33%;
    transform: scaleY(1);
  }

  .image_full {
    display: block;
    position: absolute;
  }

  .image_mobile {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .group-image {
    flex-direction: column;
    gap: 30px;
    align-items: inherit;
  }

  .content-space {
    margin: 0 24px 0 24px;
  }

  .team-image {
    width: 195px;
    height: 195px;
  }

  .address-text {
    width: 40%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-right: 10px;
  }

  .english-content {
    font-size: 16px;
    padding-bottom: 100px;
    line-height: 33px;
  }

  .heading-title {
    margin-top: 20px;
    font-size: 16px;
  }

  .info-text {
    font-size: 20px;
  }

  .team-name {
    font-size: 14px;
  }

  .team-role {
    padding-top: 5px;
    font-size: 18px;
  }

  .group-header {
    padding-bottom: 20px;
  }

  .background-image {
    display: none;
  }

  .background-image-team {
    background: url("./icon//bg.svg") no-repeat;
    background-size: 500% 190px;
  }

  .image_full {
    display: none;
  }

  .image_mobile {
    display: block;
  }

  .japan-content {
    font-size: 11px;
    display: none;
  }

  .japan-content-mobile {
    display: flex;
    align-items: center;
    font-size: 17px;
    justify-content: center;
    font-family: "PixelMplus12";
    line-height: 38px;
  }

  .header-button {
    height: 0px;
  }

  .heading-content {
    font-size: 22px;
    padding-bottom: 50px;
  }

  .heading-content-second {
    font-size: 10px;
    padding-bottom: 30px;
  }

  .btn-connect {
    font-size: 16px;
  }

  .price-info {
    font-size: 12px;
  }

  .btn-loading {
    font-size: 16px;
    height: 45px;
    width: 103px;
  }

  .btn-grad {
    font-size: 16px;
    height: 45px;
    width: 90px;
  }

  .text-mint {
    padding-top: 4px;
  }

  .footer-content {
    font-size: 10px;
  }

  .gif-icon {
    width: 94px;
    height: 94px;
  }

  .detail-info {
    margin-top: 40px;
  }

  .group-container-info {
    padding-bottom: 25px;
    transform: translateX(-55px);
  }

  .info-container {
    height: 219px;
    background-position-x: unset;
  }

  .group-first-content {
    padding-top: 67px;
  }

  .heading-content-second {
    font-size: 22px;
  }

  .land-divide {
    height: 42px;
  }

  .group-content {
    margin-top: 55px;
  }

  .group-footer {
    margin-top: 80px;
  }

  .triangle-icon {
    width: 12px;
    height: 18px;
  }

  .group-button {
    padding-right: 10px;
  }
}

@media only screen and (min-width: 440px) and (max-width: 767px) {
  .background-image-team {
    background: url("./icon//bg.svg") no-repeat;
    background-size: 300% 200px;
  }
}
